import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {ChequeBookServiceProvider} from "../../providers/cheque-book-service/cheque-book-service";
import {TokenServiceProvider} from "../../providers/token-service/token-service";



@IonicPage()
@Component({
  selector: 'page-issue-cheque-book03',
  templateUrl: 'issue-cheque-book03.html',
})
export class IssueChequeBook03Page  {
  issueChequeBookError03;

  accountNo;
  leafCount;
  formVal = {tpin: "", otp: ""};
  otp = "";

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public chqBookService: ChequeBookServiceProvider,
              public tokenService: TokenServiceProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.leafCount = JSON.parse(this.navParams.get("leafCount"));
  }


  issueChequeBook03Action = function () {
    this.issueChequeBookError03 = "";
    let flag = true;
    this.otp = this.formVal.otp;

    let validateOtpExp = new RegExp("[0-9]{6}$");

    if (this.leafCount == "" || typeof(this.leafCount) == "undefined")
    {
      this.issueChequeBookError03 = "Please select no of leaf.";
      flag = false;
      return;
    }

    if (this.otp == "" || typeof(this.otp) == "undefined")
    {
      this.issueChequeBookError03 = "Please Enter OTP.";
      flag = false;
      return;
    }


    if (!validateOtpExp.test(this.otp))
    {
      this.issueChequeBookError03 = "Invalid OTP.";
      flag = false;
      this.formVal = {otp: ""};
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encSrcAccNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encOTP = this.cryptoUtil.getEncryptVal(this.otp);
      console.log(this.otp);
      console.log(this.leafCount);
      let encLeaf = this.cryptoUtil.getEncryptVal(this.leafCount.toString());

      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.chqBookService.issueChqBook(unMaskToken, appData.userId, appData.appId, encSrcAccNo, encLeaf, encOTP)
        .then(data => {
          this.onIssueChequeBookComplete(data);
        });
    }
  };

  onIssueChequeBookComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('IssueChequeBook04Page', {
        'accountNo': this.accountNo,
        'leafCount': this.leafCount
      });
    }

    else if (data.statusCode == 0) {
      this.issueChequeBookError03 = data.note;
    }

    else {
      this.issueChequeBookError03 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}




